#navbar {
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 1030;
  #pre-nav {
    background-color: rgba($color: #00C0E4, $alpha: 0.8);
    color: #fff;
    height: 35px;
    display: flex;
    align-items: center;
    a {
      text-decoration: none;
      color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.3s;
      &:hover {
        color: #2F4B91;
      }
      img {
        height: 16px;
      }
      span {
        margin-left: 0.5rem;
      }
    }
    .container {
      .left,
      .right {
        display: flex;
      }
    }
  }
  
  .navbar {
    background-color: transparent;
    font-family: Roboto;
    font-weight: normal;
    transition: background-color 0.3s;
    @media (min-width: 1200px) {
      height: 100px;
    }
    .navbar-brand {
      .logo-navbar {
        height: 50px;
        @media (min-width: 1200px) {
          height: 80px;
        }
      }
    }
    .navbar-toggler {
      border-color: rgba($color: $white, $alpha: 0.55);
      &:focus {
        box-shadow: none;
      }
      .navbar-toggler-icon {
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28255, 255, 255, 0.55%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
      }
    }
    .navbar-nav {
      .nav-item {
        padding-left: 0.5rem;
        padding-right: 0.5rem;
        display: flex;
        justify-content: center;
        @media (min-width: 1400px) {
          padding-left: 1rem;
          padding-right: 1rem;
        }
        &.dropdown {
          .nav-link {
            .arrow-nav-down {
              width: 0.8rem;
              margin-left: 0.5rem;
            }
          }
        }
        .nav-link {
          color: $white;
          text-align: center;
          transition: all 0.3s;
          font-size: .8rem;
        }
        .search-nav {
          width: 1.2rem;
          transition: all 0.3s;
          &:hover {
            transform: scale(1.2);
          }
        }
        &:hover {
          .nav-link {
            color: $blue-siroco;
          }
        }
      }
    }
  }
}