.section-welcome {
  background: #0292eb;
  background-size: cover;
  padding-top: calc(135px + 10rem);
  padding-bottom: 3rem;
  background-position: center;
  .title-welcome {
    font-family: "Montserrat";
    font-size: 3rem;
    text-align: center;
    margin-bottom: 0;
    line-height: 55px;
    line-height: 45px;
    @media (max-width: 424.98px) {
      font-size: 2rem;
    }
    span.yellow {
      color: $yellow;
    }
    span.white {
      color: $white;
    }
    span.text-800 {
      font-weight: 800;
    }
    span.text-400 {
      font-weight: 400;
    }
    span.text-200 {
      font-weight: 200;
    }
  }
  .subtitulo-welcome {
    font-family: "Montserrat";
    font-weight: 400;
    font-size: 1.5rem;
    color: $white;
    text-align: center;
    margin-bottom: 0;
    @media (max-width: 424.98px) {
      font-size: 1rem;
    }
  }
  .radicalis-welcome {
    font-family: "radicalis";
    font-weight: 500;
    font-size: 3rem;
    text-align: center;
    margin-bottom: 0;
    color: $yellow;
    transform: translate(0,-20%) rotate(-3deg);
    letter-spacing: 2px;
    @media (max-width: 424.98px) {
      font-size: 2rem;
    }
  }
}